import { createSlice } from '@reduxjs/toolkit';

const initialState = {
    tasks: [
        { name: '1st task', id: '12kjhnreyvwsjviguyter' },
        { name: '2nd task', id: '46ght4euyhbrvnbdhfrfg' },
    ],
};

export const tasksSlice = createSlice({
    name: 'tasks',
    initialState,
    reducers: {
        addTask: (state, { payload }) => {
            const task = { name: payload.name, id: payload.id };
            state.tasks.push(task);
        },
        removeTask: (state, action) => {
            const removedTask = action.payload; //action.payload = ID
            state.tasks = state.tasks.filter((item) => item.id !== removedTask);
        },
    },
});

// Action creators are generated for each case reducer function
export const { addTask, removeTask } = tasksSlice.actions;

export default tasksSlice.reducer;
