import './styles.css';
import { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {
    decrement,
    increment,
    incrementByAmount,
    decrementByAmount,
} from './redux/counter';
import { addTask, removeTask } from './redux/tasks';

import { v4 as uuidv4 } from 'uuid';

export default function App() {
    const [value, setValue] = useState(0);
    const [task, setTask] = useState('');
    const { count } = useSelector((state) => state.counter);
    const { tasks } = useSelector((state) => state.tasks);
    const dispatch = useDispatch();

    return (
        <>
            <div className='app'>
                <h1>The count is: {count}</h1>
                <button onClick={() => dispatch(decrement())}>
                    decrement - 1
                </button>
                <button onClick={() => dispatch(increment())}>
                    increment + 1
                </button>
                <br /> <br /> <hr /> <br />
                <input
                    type='number'
                    id='set_number'
                    onChange={(e) => setValue(e.target.value)}
                />
                <br /> <br />
                <button
                    onClick={() => dispatch(decrementByAmount(Number(value)))}
                >
                    decrement by
                    {` ${value}`}
                </button>
                <button
                    onClick={() => dispatch(incrementByAmount(Number(value)))}
                >
                    increment by
                    {` ${value}`}
                </button>
            </div>
            <div className='todos'>
                <h2>Todos:</h2>
                <input
                    type='text'
                    id='add_todo'
                    onChange={(e) => setTask(e.target.value)}
                    value={task}
                />
                <button
                    onClick={() => {
                        task !== '' &&
                            dispatch(addTask({ name: task, id: uuidv4() }));
                        setTask('');
                    }}
                >
                    Add task
                </button>
                <ul className='tasks'>
                    {tasks.map(({ name, id }) => (
                        <div className='task'>
                            <li key={id}>{name}</li>
                            <button onClick={() => dispatch(removeTask(id))}>
                                X
                            </button>
                        </div>
                    ))}
                </ul>
            </div>
        </>
    );
}
