import React, { useState } from 'react';

function ParallaxImage() {
    const [position, setPosition] = useState({ mouseX: 0, mouseY: 0 });

    const handleMouseMove = (e) => {
        // Oblicz pozycję myszy w odniesieniu do środka obrazu
        const { clientX, clientY } = e;
        const { left, top, width, height } = e.target.getBoundingClientRect();
        const centerX = left + width / 2;
        const centerY = top + height / 2;
        const mouseX = clientX - centerX;
        const mouseY = clientY - centerY;

        // Zaktualizuj stan z pozycją myszy
        setPosition({ mouseX, mouseY });
    };

    return (
        <>
            <p className='icon'>X</p>
            <div className='parallax-container' onMouseMove={handleMouseMove}>
                <p className='name'>
                    HUBERT <span>ŁEPSKI</span>
                </p>
                <div
                    className='parallax-image'
                    style={{
                        transform: `translate(${position.mouseX / 75}px, ${
                            position.mouseY / 75
                        }px)`,
                    }}
                >
                    <div className='sculpture' />
                </div>
            </div>
        </>
    );
}

export default ParallaxImage;
